html {
  font-size: 100%;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*, *::before, *::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: "Barlow", sans-serif;
  font-family: "Bungee", cursive;
  background-color: #141414;
}

section {
  margin: 7%;
  margin-bottom: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
@media (min-width: 1440px) {
  section {
    margin: 7% 20%;
  }
}

header {
  padding: 0 7%;
  display: flex;
  flex-direction: row;
  height: 5rem;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  background-color: #141414;
  top: 0;
  position: fixed;
  z-index: 2;
}
@media (min-width: 1440px) {
  header {
    padding: 0 20%;
  }
}
header .open span:first-child {
  transform: translateY(0.25rem);
  transform: rotate(45deg);
}
header .open span:nth-child(2) {
  opacity: 0;
}
header .open span:last-child {
  transform: rotate(-45deg);
}
header a {
  display: flex;
  align-items: center;
  padding: 0;
  margin: 0;
}
header .hamburger {
  display: flex;
  flex-direction: column;
  cursor: pointer;
}
@media (min-width: 1024px) {
  header .hamburger {
    display: none;
  }
}
header .hamburger span {
  height: 0.125rem;
  width: 1.375rem;
  background-color: #FDF9F2;
  transition: all 300ms ease-in-out;
  transform-origin: left center;
}
header .hamburger span:not(:last-child) {
  margin-bottom: 0.375rem;
}
header .nav {
  transition: all 300ms ease-in-out;
}
@media (max-width: 1023px) {
  header .nav {
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0;
    width: 100%;
    position: absolute;
    top: 5rem;
    right: -100vw;
  }
}
header .nav.open {
  display: flex;
  align-items: center;
  justify-content: center;
  padding-top: 1.875rem;
  padding-bottom: 1.875rem;
  background-color: #D9310B;
  z-index: -1;
  position: absolute;
  transform: translateX(-rem(1415));
  right: 0;
}
header .nav ul {
  display: flex;
  flex-direction: row;
  text-decoration: none;
  align-items: center;
  justify-content: space-between;
}
@media (max-width: 1023px) {
  header .nav ul {
    flex-direction: column;
  }
}
header .nav ul li {
  font-family: "Barlow", sans-serif;
  font-weight: 400;
  font-size: 1.125rem;
  list-style: none;
}
@media (max-width: 1023px) {
  header .nav ul li {
    font-family: "Bungee", cursive;
    font-size: 1.25rem;
    list-style: none;
  }
}
header .nav ul li:not(:last-child) {
  margin-right: 2.1875rem;
}
@media (max-width: 1023px) {
  header .nav ul li:not(:last-child) {
    margin-right: 0;
  }
}
header .nav ul li a {
  transition: all 0.3s ease-in-out;
  color: #FDF9F2;
  text-decoration: none;
}
header .nav ul li a:hover {
  color: #D9310B;
}
@media (max-width: 1023px) {
  header .nav ul li a:hover {
    color: #141414;
  }
}
header .nav ul li a.checked {
  color: #D9310B;
  font-weight: 700;
}
@media (max-width: 1023px) {
  header .nav ul li a.checked {
    color: #141414;
  }
}

footer {
  margin: 6.25rem 0;
  display: flex;
  align-items: center;
  justify-content: center;
}
@media (max-width: 1023px) {
  footer {
    flex-direction: column;
  }
}
footer .logo {
  margin: 0;
  margin-right: 10%;
}
@media (max-width: 1023px) {
  footer .logo {
    margin-right: 0;
    margin-bottom: 1.5625rem;
  }
}
footer .logo p {
  color: #8C2D18;
  text-decoration: underline;
}
@media (max-width: 1023px) {
  footer .logo p {
    font-size: 0.875rem;
  }
}
footer .logo a {
  top: 0;
}
@media (max-width: 1023px) {
  footer .logo img {
    width: 11.25rem;
    height: auto;
  }
}
footer .footer-nav ul {
  display: flex;
  flex-direction: column;
  text-decoration: none;
  align-items: center;
  justify-content: center;
}
@media (max-width: 1023px) {
  footer .footer-nav ul {
    padding-left: 0;
  }
}
footer .footer-nav ul li {
  font-family: "Barlow", sans-serif;
  font-weight: 400;
  font-size: 1.125rem;
  list-style: none;
}
footer .footer-nav ul li:not(:last-child) {
  margin-bottom: 1.25rem;
}
footer .footer-nav ul li a {
  color: #FDF9F2;
  text-decoration: none;
  transition: all 300ms ease-in-out;
}
footer .footer-nav ul li a:hover {
  color: #D9310B;
}
footer .footer-nav ul li a .checked {
  color: #D9310B;
  font-weight: 700;
}

@media (max-width: 1439px) {
  .hide-for-mobile {
    display: none;
  }
}

@media (min-width: 1024px) {
  .hide-for-desktop {
    display: none;
  }
}

h1, h2, h3 {
  font-family: "Bungee", cursive;
  color: #FDF9F2;
  margin: 0;
  pointer-events: none;
}

h1 {
  font-size: 4rem;
  line-height: 4.8125rem;
  margin-bottom: 0.9375rem;
}
h1 span {
  color: #D9310B;
}
@media (max-width: 1023px) {
  h1 {
    font-size: 2.8125rem;
    margin-bottom: 0.5625rem;
    line-height: 3.375rem;
  }
}
@media (max-width: 639px) {
  h1 {
    font-size: 2.1875rem;
    margin-bottom: 0.5625rem;
    line-height: 2.625rem;
  }
}

h2 {
  font-size: 2.5rem;
  line-height: 3rem;
  margin-bottom: 3.125rem;
}
h2.text {
  margin: 0;
  margin-bottom: 0.9375rem;
}
@media (max-width: 1023px) {
  h2.text {
    margin-bottom: 0.9375rem;
  }
}
@media (max-width: 639px) {
  h2.text {
    margin-bottom: 0.9375rem;
  }
}
@media (max-width: 1023px) {
  h2 {
    font-size: 1.875rem;
    margin-bottom: 1.5625rem;
    line-height: 2.25rem;
  }
}
@media (max-width: 639px) {
  h2 {
    font-size: 1.375rem;
    margin-bottom: 1.5625rem;
    line-height: 1.625rem;
  }
}

h3 {
  font-size: 1.875rem;
  line-height: 2.25rem;
  margin-bottom: 0.9375rem;
  color: #8C2D18;
}
@media (max-width: 1023px) {
  h3 {
    font-size: 1.375rem;
    margin-bottom: 0.9375rem;
    line-height: 1.5rem;
  }
}
@media (max-width: 639px) {
  h3 {
    font-size: 1.25rem;
    margin-bottom: 0.9375rem;
    line-height: 1.375rem;
  }
}

p {
  font-family: "Barlow", sans-serif;
  font-size: 1.125rem;
  font-weight: 400;
  color: #FDF9F2;
  line-height: 1.8125rem;
  margin: 0;
  pointer-events: none;
}
@media (max-width: 1023px) {
  p {
    font-size: 1rem;
    line-height: 1.5rem;
  }
}
@media (max-width: 639px) {
  p {
    font-size: 0.875rem;
    line-height: 1.3125rem;
  }
}

a, a:visited, a:active {
  text-decoration: none;
}

.div-button {
  margin-top: 2.8125rem;
}

.button-large {
  transition: all 0.3s ease-in-out;
  font-family: "Bungee", cursive;
  font-size: 1.375rem;
  color: #141414;
  padding: 0rem 5rem;
  background-color: #D9310B;
}
.button-large:hover {
  opacity: 70%;
}
@media (max-width: 1023px) {
  .button-large {
    font-size: 1.125rem;
    padding: 0.3125rem 2.8125rem;
  }
}
@media (max-width: 639px) {
  .button-large {
    font-size: 0.875rem;
    padding: 0.3125rem 2.8125rem;
  }
}
.button-unfilled-red {
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-top: 0.9375rem;
}
.button-unfilled-red p {
  font-weight: 600;
  margin-right: 0.9375rem;
  color: #8C2D18;
}
@media (max-width: 1023px) {
  .button-unfilled-red p {
    white-space: nowrap;
  }
}
.button-unfilled-red svg path {
  stroke: #8C2D18;
}
.button-unfilled-white {
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-top: 1.25rem;
}
.button-unfilled-white p {
  margin: 0;
  font-weight: 600;
  margin-right: 0.9375rem;
  color: #FDF9F2;
}
@media (max-width: 1023px) {
  .button-unfilled-white p {
    white-space: nowrap;
  }
}

.card-wrap-photo-bg {
  overflow: hidden;
  transition: all 0.3s ease-in-out;
}
.card-wrap-photo-bg:hover .card-photo-bg {
  transform: scale(1.15);
  transition: all 0.3s ease-in-out;
}
@media (min-width: 1024px) {
  .card-wrap-photo-bg:hover .card-photo-bg {
    padding-left: 10%;
  }
}
.card-wrap-photo-bg:hover .card-side-to-side {
  transform: scale(1.15);
  transition: all 0.3s ease-in-out;
}
@media (min-width: 1440px) {
  .card-wrap-photo-bg:hover .card-side-to-side {
    padding-left: 10%;
  }
}
.card-photo-bg {
  width: 100%;
  height: 19.6875rem;
  padding: 0;
  padding-left: 3.125rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  background-size: cover;
  background-position: center;
  transition: all 0.3s ease-in-out;
}
.card-photo-bg h3 {
  color: #FDF9F2;
}
.card-photo-bg p {
  max-width: 50%;
}
@media (max-width: 1023px) {
  .card-photo-bg p {
    max-width: 80%;
  }
}
@media (max-width: 639px) {
  .card-photo-bg p {
    max-width: 100%;
  }
}
@media (max-width: 1023px) {
  .card-photo-bg {
    text-align: center;
    align-items: center;
    padding: 5rem 1.25rem;
  }
}
.card-text {
  transition: all 0.3s ease-in-out;
  width: 100%;
  height: 19.6875rem;
  padding: 0;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}
.card-text:hover {
  transform: translateX(10%);
}
@media (max-width: 1023px) {
  .card-text {
    margin-bottom: 0;
    height: 15.625rem;
  }
}
.card-text div {
  max-width: 45%;
}
.card-text .image-wrap {
  height: 100%;
  width: 45%;
  z-index: -1;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
}
.card-text .image-wrap.no-bg {
  background-size: contain;
  background-repeat: no-repeat;
}
.card-side-to-side {
  transition: all 0.3s ease-in-out;
  height: 25rem;
  padding: 0 7%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  background-size: cover;
  background-position: center;
}
@media (min-width: 640px) {
  .card-side-to-side p {
    width: 60%;
  }
}
@media (min-width: 1024px) {
  .card-side-to-side p {
    width: 100%;
  }
}
@media (max-width: 1023px) {
  .card-side-to-side {
    text-align: center;
    align-items: center;
    width: 100%;
    padding: 3.75rem 1.25rem;
  }
}

.blur {
  max-width: 31.25rem;
  padding: 2.5rem 3.75rem;
  background-color: rgba(20, 20, 20, 0.7);
  backdrop-filter: blur(0.25rem);
  display: inherit;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  transition: all 0.3s ease-in-out;
}
.blur .p-text {
  max-width: 23.75rem;
  text-align: center;
}

.threejs {
