.overlays {
  pointer-events: none;
  z-index: 1;
  position: absolute;
  top: 0;
  display: flex;
  width: 100%;
}
.overlays .overlay {
  padding-left: 0.9375rem;
  width: 50%;
  background-color: rgba(14, 14, 14, 0.7);
  backdrop-filter: blur(4);
}
@media (max-width: 639px) {
  .overlays .overlay {
    display: grid;
    width: 100%;
    height: 1.375rem;
  }
}
@media (max-width: 639px) {
  .overlays {
    display: grid;
    height: 100%;
  }
}

.accordion_item {
  margin-bottom: 1%;
  background: #202020;
  padding: 1% 3.5%;
}

.accordion_header {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}
.accordion_header h3 {
  margin-bottom: 0;
  transition: color 0.3s ease-in-out;
}
.accordion_header.open h3 {
  color: #D9310B;
}
.accordion_header.open img {
  transform: rotate(180deg);
}

.accordion_content {
  transition: height 0.3s ease-in-out;
}
.accordion_content.collapsing {
  height: 0;
  overflow: hidden;
}
.accordion_content.collapse {
  display: none;
}
.accordion_content.open {
  display: block;
}
.accordion_content p:first-child {
  margin-top: 1%;
}
.accordion_content p {
  margin-bottom: 1%;
}

img.collapse_arrow {
  width: 1.5rem;
  height: 1.5rem;
  transition: all 0.3s ease-in-out;
}

.gallery-container {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: auto;
  gap: 0.9375rem;
}
@media (max-width: 1023px) {
  .gallery-container {
    grid-template-columns: repeat(3, 1fr);
  }
}
@media (max-width: 1023px) {
  .gallery-container {
    grid-template-columns: repeat(2, 1fr);
  }
}
.gallery-container .gallery-card {
  height: 19.6875rem;
  width: 100%;
  position: relative;
}
.gallery-container .gallery-card img {
  width: 100%;
  height: 19.6875rem;
  object-fit: cover;
}
.gallery-container .gallery-card .gallery-card-overlay {
  width: 100%;
  background: rgba(20, 20, 20, 0.7);
  backdrop-filter: blur(0.25rem);
  position: absolute;
  bottom: 0;
  padding: 0 0.9375rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.gallery-container .gallery-card svg {
  margin: 0;
  padding: 0;
}
.gallery-container .gallery-card a {
  display: grid;
  place-items: center;
}

